    display: inline-block;
    position: relative;
    margin-right: 4px;
    width: 3px;
    height: 1px;
    overflow: hidden;
    background: linear-gradient(to bottom, #ff9500, #ff5e3a);
    color: transparent;
    animation-name: pulse;
    animation-duration: 1s;
    animation-iteration-count: infinite
}

