@keyframes pulse {
    0% {
        height: 1px;
        margin-top: 0
    }
    10% {
        height: 15px;
        margin-top: -15px
    }
    50% {
        height: 12px;
        margin-top: -12px
    }
    60% {
        height: 17px;
        margin-top: -17px
    }
    80% {
        height: 15px;
        margin-top: -15px
    }
    100% {
